.landing .page__main {
  z-index: 2;
  color: #fff;
  height: inherit;
  position: relative;
}
.landing .page__main .page__wrapper {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 25rem;
  justify-content: center;
}

.landing__brand {
  height: 9.625rem;
  margin: 0;
  position: relative;
  width: 9.5rem;
}
.landing__brand::before, .landing__brand::after {
  background: url("../img/layout/spacenomads-logo.svg") left top no-repeat;
  background-size: 100% auto;
  content: "";
  display: block;
  height: 4.9375rem;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.landing__brand::after {
  background-position: left bottom;
  height: 4.6875rem;
  top: 4.9375rem;
}

.landing__brand-label {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  margin: 0 !important;
  overflow: hidden;
  position: absolute;
}

.landing__status {
  font-family: "Roboto Slab", arial, sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  margin: 0.3125rem 0 0;
  opacity: 0.5;
}

.ready .landing__brand::before {
  transform: translate(1500%, -1500%);
}
.ready .landing__brand::after {
  opacity: 0;
}
.ready .landing__status {
  transform: translateY(1em);
  opacity: 0;
}

.active .landing__brand::before {
  transition: transform ease 0.5s;
}
.active .landing__brand::after {
  transition: opacity ease 0.5s 0.5s;
}
.active .landing__status {
  transition: opacity ease 0.5s 1s, transform ease 1s 0.8s;
}

.has-asteroid .landing__brand::before {
  transform: translate(0, 0);
}
.has-asteroid .landing__brand::after {
  opacity: 1;
}
.has-asteroid .landing__status {
  transform: translateY(0);
  opacity: 0.45;
}

.landing .page__footer {
  z-index: 3;
  bottom: 0;
  position: fixed;
  right: 0;
}
.landing .page__footer .page__wrapper {
  align-items: flex-end;
  display: flex;
  flex-direction: column-reverse;
  padding: 1.5625rem;
}
.landing .page__footer-links {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0.3125rem;
}
.landing .page__footer-link + .page__footer-link {
  margin-left: 0.625rem;
}
.landing .page__footer-item {
  height: 0.0625rem;
  overflow: hidden;
  padding: 2.4375rem 0 0;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  display: block;
  position: relative;
  width: 2.5rem;
}
.landing .page__footer-item::before, .landing .page__footer-item::after {
  background: url("../img/layout/sprite-contact.svg") left top no-repeat;
  content: "";
  height: 1rem;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: color ease 0.5s;
  width: 1rem;
}
.landing .page__footer-item::after {
  opacity: 0;
  transition: opacity ease 0.5s;
}
.landing .page__footer-item:hover::after {
  opacity: 1;
}
.landing .page__footer-item--twitter::before {
  background-position: left top;
}
.landing .page__footer-item--twitter::after {
  background-position: left bottom;
}
.landing .page__footer-item--mail::before {
  background-position: right top;
}
.landing .page__footer-item--mail::after {
  background-position: right bottom;
}
.landing .page__footer-item--chuck {
  background: url("../img/layout/../maquetando-con-chuck/chuck.png") center no-repeat;
  background-size: cover;
}
.landing .page__footer-item--chuck::before, .landing .page__footer-item--chuck::after {
  display: none;
}
.landing .page__footer-item--with-tooltip {
  overflow: hidden;
  position: relative;
}
.landing .tooltip {
  bottom: 100%;
  color: white;
  font-weight: 900;
  left: 0;
  opacity: 0;
  padding: 0.3125rem 0;
  position: absolute;
  transition: opacity ease 0.3s;
}
.landing .page__footer-item--with-tooltip:hover {
  overflow: visible;
}
.landing .page__footer-item--with-tooltip:hover .tooltip {
  opacity: 1;
}

.chuck {
  background: #fff;
  color: #000;
  font-family: "Roboto Slab", arial, sans-serif;
}
.chuck .page__wrapper {
  margin: 0 1.25rem;
}
@media screen and (min-width: 48em) {
  .chuck .page__wrapper {
    margin: 0 auto;
    max-width: 41.75rem;
  }
}
.chuck .page {
  display: flex;
  flex-direction: column;
}
.chuck .page__main {
  flex-grow: 1;
}

.chuck-header {
  background: radial-gradient(99.54% 99.54% at 49.43% 50.46%, #79c8f8 0%, #008aff 100%);
  color: #fff;
  font-family: "Roboto", arial, sans-serif;
}
.chuck-header .page__wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  padding: 2.8125rem 0 3.75rem;
  position: relative;
  box-sizing: border-box;
  min-height: 35rem;
}
@media screen and (min-width: 33.75em) {
  .chuck-header .page__wrapper {
    column-gap: 1.25rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-content: center;
  }
}

.chuck-header__content {
  margin: 1.25rem auto 0;
  order: 1;
  width: 12.3125rem;
}
@media screen and (min-width: 33.75em) {
  .chuck-header__content {
    margin-left: 0;
  }
}

.chuck-header__title {
  font-size: 2.125rem;
  font-weight: 900;
  letter-spacing: 0.0357143125rem;
  line-height: 126.6666666667%;
  margin: 0 0 0.3125rem;
}
@media screen and (min-width: 33.75em) {
  .chuck-header__title {
    font-size: 2.25rem;
  }
}

.chuck-header__streaming {
  border: 0.0625rem solid #fff;
  border-width: 0.0625rem 0;
  display: inline-block;
  margin: 0 0 0.3125rem;
  padding: 0.3125rem 0;
}

.chuck-header__streaming-link {
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 0.019047625rem;
  line-height: 125%;
  text-decoration: none;
  text-transform: lowercase;
}

.chuck-header__streaming-cal {
  font-size: 1rem;
  letter-spacing: 0.019047625rem;
  line-height: 125%;
  margin: 0;
}

.chuck-header__avatar-item {
  margin: auto;
  padding: 1rem;
  position: relative;
  width: 10.3125rem;
}
@media screen and (min-width: 33.75em) {
  .chuck-header__avatar-item {
    width: 13.75rem;
  }
}
.chuck-header__avatar-item::before {
  box-shadow: 0 0 0 1px #fff;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 2;
}

.chuck-header__avatar-img {
  box-shadow: 0 1.5625rem 1.875rem #1b72b4;
  display: block;
  position: relative;
  width: 100%;
  z-index: 1;
}

.page__social {
  margin-top: 0.625rem;
}

.chuck-header__next {
  position: absolute;
  left: 50%;
  bottom: 1.25rem;
  transform: translateX(-50%);
}
.chuck-header__next::before, .chuck-header__next::after {
  position: absolute;
  left: 50%;
  top: 50%;
  content: "{";
  transform: translate(-50%, -50%);
  margin-left: -20px;
  margin-top: -0.125rem;
  font-size: 2em;
  opacity: 0.5;
  font-weight: 300;
  animation: next-l 2.5s both infinite;
}
.chuck-header__next::after {
  content: "}";
  transform: translate(-50%, -50%);
  margin-left: 20px;
  animation: next-r 2.5s both infinite;
}

.chuck-header__next-link {
  display: block;
  height: 0.0625rem;
  width: 2.5rem;
  padding-top: 2.4375rem;
  overflow: hidden;
  background: url("../img/maquetando-con-chuck/ico-next.svg") left top no-repeat;
}

@keyframes next-l {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.2);
    margin-left: -30px;
  }
}
@keyframes next-r {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.2);
    margin-left: 30px;
  }
}
